
import { onMounted, ref } from "vue";
import AOS from "aos";
import "aos/dist/aos.css";

export default {
  name: "AppAcademic",
  setup() {
    const isMounted = ref(false);

    onMounted(() => {
      AOS.init();
      isMounted.value = true;
    });

    return {
      isMounted,
    };
  },
};
